































import Vue from "vue";
export default Vue.extend({
  props: {
    value: {
      type: String,
      default: () =>
        new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
          .toISOString()
          .substr(0, 10),
    },
    label: String,
  },
  data() {
    return {
      max: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
        .toISOString()
        .substr(0, 10),
      date: this.value,
      modal: false,
    };
  },
  watch: {
    date(value) {
      this.$emit("input", value);
    },
  },
});
