


























































































import Vue from "vue";
import { getTypes } from "@/mixins/utils";
import rules from "@/mixins/rules";
import { removerMask, getUFs } from "@/mixins/utils";
import { mapState } from "vuex";
import http from "@/service/axios";
import editMap from "@/components/maps/edit.vue";

export default Vue.extend({
  mixins: [rules],
  components: {
    editMap,
  },

  computed: {
    ...mapState("farm", ["modal", "status"]),
    ...mapState("auth", ["local"]),
    types() {
      return getTypes();
    },
    isMobile() {
      return this.$vuetify.breakpoint.width <= 700;
    },
    Ufs() {
      const ufs = Object.keys(getUFs());
      ufs.splice(0, 1);
      return ufs;
    },
    cmpModal: {
      get() {
        return this.modal === "NEW";
      },
      set(value) {
        if (!value) this.$store.dispatch("farm/CHANGE", { modal: "" });
      },
    },
  },
  data: () => ({
    name: "",
    cep: "",
    address: "",
    district: "",
    city: "",
    state: "",
    nirf: "",
    loadcep: false,
    indraw: false,
    circle: {} as any,
    invalidLocal: false,
  }),
  watch: {
    indraw() {
      this.invalidLocal = false;
    },
  },
  methods: {
    closeModal() {
      this.$router.push({ name: "dashboard" });
    },
    createFarm() {
      if (
        !(this.$refs.form as Vue & {
          validate: () => boolean;
        }).validate()
      ) {
        return;
      }
      if (Object.keys(this.circle).length === 0) {
        this.invalidLocal = true;
        return;
      }
      const userId = this.$store.getters["auth/userId"];
      const payload = {
        name: this.name,
        userId: userId,
        street: this.address,
        district: this.district,
        city: this.city,
        cep: this.cep,
        state: this.state,
        nirf: this.nirf,
        position: {
          radius: this.circle.radius,
          lat: this.circle.lat,
          long: this.circle.lng,
        },
      };
      this.$store.dispatch("farm/NEW_FARM", payload);
    },
    searchCEP() {
      if (!this.cep) return;
      this.loadcep = true;

      http
        .get("https://ws.apicep.com/cep/" + removerMask(this.cep) + ".json")
        .then((res) => {
          if (!res.data.ok) {
            this.$toast.error(res.data.message);
          } else {
            this.address = res.data.address;
            this.district = res.data.district;
            this.city = res.data.city;
            this.state = res.data.state;
          }
        })
        .catch((err) => {
          this.$toast.error("Ocorreu um erro ao buscar o CEP");
        })
        .finally(() => {
          this.loadcep = false;
        });
    },
  },
});
